import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CoreModule } from 'core/core.module';
import { CommonComponentsModule } from 'common/common.module';

import { ManageAdminRoutingModule } from './manage-admin-routing.module';
import { ManageAdminComponent } from './manage-admin.component';
import { AddEditAdminComponent } from './add-edit-admin/add-edit-admin.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    CoreModule,
    CommonComponentsModule,
    ManageAdminRoutingModule
  ],
  declarations: [ManageAdminComponent,AddEditAdminComponent]
})
export class ManageAdminModule { }



