import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Modal, overlayConfigFactory } from 'angular2-modal';
import { BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { Constants, PermissionType } from 'core/constants';
import { CropperSettings } from 'core/cropper/cropperSettings';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { LocalStorageService } from 'core/service/local-storage.service';
import { ImagePopupComponent } from 'imageCroppingPopup/imageCrop.component';
import { ImageResult } from 'ng2-imageupload';
import { ConfirmpopupComponent } from './../../common/confirmpopup/confirmpopup.component';
import { ActivatedRoute, Router } from '@angular/router';
import { CompleterService, CompleterData } from '../../core/autocomplete';
import { HttpCommonUtilsService } from 'core/http_wrapper/http-common-utils.service';
import { UserSession } from 'core/sessions';
import { ActivitiesListData } from 'core/models/activitiesListResponse';
import { ClassCategoryList } from 'core/models/trainerDetailResponse';
import { HttpUserUtilsService } from 'core/http_wrapper/http-user-utils.service';
import { BaseComponent } from 'app/base/base.component';
import { element } from 'protractor';

@Component({
  selector: 'app-add-edit-admin',
  templateUrl: './add-edit-admin.component.html',
  styleUrls: ['./add-edit-admin.component.css']
})

export class AddEditAdminComponent extends BaseComponent implements OnInit,HttpSuccesFailureResponse  {
  adminForm: FormGroup;
  userData:any;
  allPermissionPagesData=[];
  headerTitleGYMRegular=[{name:"Page",view:false,id:0},{name:"Revenue Per Session",view:false,id:0},{name:"Commision Type",view:false,id:0},{name:"Commision Value",view:false,id:0},{name:"Adjust Based On Discount",view:false,id:0},{name:"Commision Amount",view:false,id:0}]
  currentAdminId;
  isValidEmail = false;
  isEmailAlreadyExist: boolean=false;

  constructor(public modal: Modal,private http_user: HttpUserAuthUtilsService,    private formBuilder: FormBuilder
  
    ,private route: ActivatedRoute,private router: Router,    private common_http: HttpCommonUtilsService,
    private completerService: CompleterService,
    private user_http: HttpUserUtilsService

    ) {super(http_user) }  
 
  
  ngOnInit() {
    this.getParamFromRoute();
  }
  getParamFromRoute() {
    this.route.params.subscribe(params => {
      
      if(params['id']&&params['id']!='add'){
         this.currentAdminId = params['id'] || null;
        this.getuserprofile();

      }else{
        this.http_user.getAllPagesPermission(this,true);
      }
    });
  }
  getuserprofile() {
    
   this.userData = JSON.parse(sessionStorage.getItem('adminDataTransfer'));
   this.http_user.getAllPagesPermission(this,true);

// this.http_user.getAdminProfile(this.currentAdminId, this, true);
  }
  initAdminForm() {
    this.adminForm =  this.formBuilder.group({
      adminName: [this.userData?this.userData.name:'',Validators.required],
      email: [this.userData?this.userData.email:'',Validators.required],
      mobile: [this.userData?this.userData.mobile:'',Validators.required],
      adminPassword: ['',Validators.required],
      permissions: this.formBuilder.array([])
    });
    if(this.userData){
    this.adminForm.controls['adminPassword'].clearValidators();
    this.adminForm.controls['adminPassword'].updateValueAndValidity();}
  }
  get permissionArray(): FormArray {
    return this.adminForm.get('permissions') as FormArray;
  }
  initPermissionForm(element:any) {
   let permissionForm =  this.formBuilder.group({
         permissionType: element.permissionType?element.permissionType:PermissionType.NOACCESS,
         name:element.name,
         id:element.id,
        pageId:element.pageId});
    return permissionForm;
  }
  cancelForm(){
    this.router.navigate(['/manageAdmin']);
  }
  isUpdatingSameEmail(){
    if(this.userData){
      return this.userData.email == this.adminForm.value.email;
    }else{
      return false;
    }
  }
  saveData(){
    this.isEmailAlreadyExist=false;
    if(this.isUpdatingSameEmail()){
      this.isValidEmail = true;
      this.isEmailAlreadyExist = false;
    }
    if(this.adminForm.valid){
      let formValue = this.adminForm.value;
      if(!this.isValidEmail){
        this.common_http.validate('email', formValue.email, this, true);
        return;
      }
      let adminMappingList = [];
      formValue.permissions.forEach(element => {
        if(this.userData){
          adminMappingList.push({"adminId":"","pageId":element.pageId,"id":element.id,"permissionType":element.permissionType});

        }else{
          adminMappingList.push({"adminId":"","pageId":element.id,"permissionType":element.permissionType});

        }
      });
      let request;
      if(this.userData){
         request = {"id":this.userData.id,"name":formValue.adminName,"email":formValue.email,"mobile":formValue.mobile,"gymId":LocalStorageService.getUserId(),
        "adminPermissionMappingList":adminMappingList
   }
   this.http_user.updateAdmin(request, this, true)

      }else{
         request = {"name":formValue.adminName,"email":formValue.email,"mobile":formValue.mobile,"gymId":LocalStorageService.getUserId(),"password":formValue.adminPassword,
        "adminPermissionMappingList":adminMappingList
   }
   this.http_user.createAdmin(request, this, true)

      }
 
    }else{
      this.markFormGroupTouched(this.adminForm);
    }
    
  }
  onCheckBoxCheked(event,index,permvalue){
    let value = event.target.checked;
    if(value){
      this.permissionArray.controls[index].patchValue({permissionType:permvalue});

    }else{
      this.permissionArray.controls[index].patchValue({permissionType:PermissionType.NOACCESS});
    }
  
  }
  onSuccess(type: any, responsedata: any) {
    if (type == Constants.ValidateApi) {
      this.isValidEmail = true;
      this.isEmailAlreadyExist=false;

         this.saveData();
        }
    if(type == Constants.AllPagesPermission){
      this.allPermissionPagesData = responsedata.data;
      let mergedPermision = this.allPermissionPagesData;
      this.initAdminForm();
      if(this.userData){
      this.allPermissionPagesData.forEach(element => {
        this.userData.adminPermissionMappingList.forEach(mapping => {
           element.pageId = mapping.id;
        });
        
      });
       mergedPermision = this.mergePermission(this.allPermissionPagesData,this.userData.adminPermissionMappingList)

    }
    mergedPermision.forEach(element => {
        this.permissionArray.push(this.initPermissionForm(element));
        
      });
    }
    if (type === Constants.FindUserApi) {
      this.userData = responsedata.data;
      this.http_user.getAllPagesPermission(this,true);

    }
    if (type === Constants.createAdminApi) {
      this.router.navigate(['/manageAdmin']);
      // this.userData = responsedata.data;
      // this.http_user.getAllPagesPermission(this,true);

    }
    if (type === Constants.updateAdminApi) {
      this.router.navigate(['/manageAdmin']);
      // this.userData = responsedata.data;
      // this.http_user.getAllPagesPermission(this,true);

    }
    }
  onFailure(type: any, response: any) {
    if (type == Constants.ValidateApi) {
       
   
      this.isEmailAlreadyExist=true;
      this.isValidEmail = false;
     
}
    if (type === Constants.createAdminApi) {
      alert(response);
    //  this.router.navigate(['/manageAdmin']);
      // this.userData = responsedata.data;
      // this.http_user.getAllPagesPermission(this,true);

    }
    if (type === Constants.updateAdminApi) {
      alert(response);
   
    }
    if(type == Constants.AllPagesPermission){
       alert(response);
    } 
    if (type === Constants.FindUserApi) {
      alert(response);

    }
  
  }

    private markFormGroupTouched(formGroup: FormGroup) {
      try {
        (<any>Object).values(formGroup.controls).forEach(control => {
          control.markAsTouched();
          if (control.controls) {
            control.controls.forEach(c => this.markFormGroupTouched(c));
          }
        });
      } catch (ex) { }
    }
}



