import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Modal, overlayConfigFactory } from 'angular2-modal';
import { BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { GlobalSubscriptionService } from 'app/globalSubcription.service';
import { ConfirmpopupComponent } from 'common/confirmpopup/confirmpopup.component';
import { Constants } from 'core/constants';
import { HttpUserUtilsService } from 'core/http_wrapper/http-user-utils.service';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { TrainerGymDetail } from 'core/models/gymtrainerDetailResponse';
import { LocalStorageService } from 'core/service/local-storage.service';

@Component({
  selector: 'app-manage-admin',
  templateUrl: './manage-admin.component.html',
  styleUrls: ['./manage-admin.component.css']
})
export class ManageAdminComponent implements OnInit,HttpSuccesFailureResponse {

  currentPage = 0;
  trainerList: any[] = [];
  headerTitleGYMRegular=[{name:"Admin Name",view:false,id:0},{name:"Email",view:false,id:0},{name:"Mobile Number",view:false,id:0},{name:"Permissions",view:false,id:0},{name:"Action",view:false,id:0}]
  listIsLaoding: boolean = false;
  adminTobeDeleted;
  constructor(private modal: Modal,private globalSubscription: GlobalSubscriptionService, private _activatedRoute: ActivatedRoute, private router: Router, private globalSubscriptionService: GlobalSubscriptionService, private user_http: HttpUserUtilsService, private http_userauth: HttpUserAuthUtilsService) { }
 

  ngOnInit() {
    this.trainerListByGym();
  }
  trainerListByGym() {
    this.listIsLaoding = true;
    this.http_userauth.getAllGymAdmin(LocalStorageService.getUserId(), this.currentPage, this, true)
 
}
routeToTrainerDetails(value?: any) {
  if(value){
    sessionStorage.setItem('adminDataTransfer', JSON.stringify(value));
    this.router.navigate(['/manageAdmin', value.id]);

  }else{
    this.router.navigate(['/manageAdmin/add']);

  }

}

deleteAdmin(){
  this.http_userauth.deleteAdmin(this.adminTobeDeleted.id, this, true);
}

deleteAdminMethod(item){
  this.adminTobeDeleted = item;

  this.openconfirmpopup(`Are you sure you want to delete ${item.name} ?`, 'Delete Admin','Yes','No', this, 'deleteAdmin');

}

openconfirmpopup(message, title,yesTitle,noTitle, parent?: any, errorCode?: any) {
  return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title ,yesTitle,noTitle, parent, errorCode}, BSModalContext));

}
onSuccess(type: any, responsedata: any) {
  if(type == Constants.deleteAdminAPI){
    this.trainerList = [];
    this.trainerListByGym();
  }
  if (type === Constants.AllAdminApi) {
    this.listIsLaoding = false;
    const temp_list: any[] = responsedata;
    temp_list.forEach(element => {
      let permissions = '';
      if(element.adminPermissionMappingList){
      element.adminPermissionMappingList.forEach(element => {
        permissions += element.pageName + ',';
        
      });}
      element.permissions = permissions;
       this.trainerList.push(element);
     });

    



}
}
onFailure(type: any, response: any) {
  if (type === Constants.AllTrainerApi) {
    this.listIsLaoding = false;




}if(type == Constants.deleteAdminAPI){
  alert(response);
}
}
}



