import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ManageAdminComponent } from './manage-admin.component';
import { AddEditAdminComponent } from './add-edit-admin/add-edit-admin.component';

const routes: Routes = [
  { path: '', component: ManageAdminComponent },
  {path:':id',component:AddEditAdminComponent},

  {path:'add',component:AddEditAdminComponent},

];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ManageAdminRoutingModule { }



